.label {
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
    padding: .7rem 1.5rem;
    font-weight: normal;
}

.label:hover {
    background-color: var(--lightBlue);
}

.label input {
    display: none;
}

.customInput {
    display: inline-block;
    position: relative;
    width: 1.5rem;
    height: 1.5rem;
    border: 1px solid var(--grey);
    margin-right: 1rem;
    border-radius: .2rem;
}

.label input:checked ~ .customInput {
    border: 1px solid var(--blue);
}

.customInput:after {
    content: "";
    position: absolute;
    display: none;
}

.label input:checked ~ .customInput:after {
    display: block;
}

